'use client';

import { useState, useRef } from 'react';
import axios from 'axios';
import dynamic from 'next/dynamic';
import { ClipLoader } from 'react-spinners';
import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';

const ActorList = dynamic(() => import('../components/ActorList'), { ssr: false });
const SchemaEditor = dynamic(() => import('../components/SchemaEditor'), { ssr: false });
const DocsModal = dynamic(() => import('../components/DocsModal'), { ssr: false });

export default function Home() {
  const [apiKey, setApiKey] = useState('');
  const [actors, setActors] = useState([]);
  const [publicActors, setPublicActors] = useState([]);
  const [schema, setSchema] = useState(null);
  const [input, setInput] = useState('{}');
  const [runResult, setRunResult] = useState(null);
  const [actorId, setActorId] = useState('');
  const [loading, setLoading] = useState(false);
  const resultRef = useRef(null);

  const backend = process.env.NEXT_PUBLIC_BACKEND_URL;

  const notify = (msg, type = "info") => toast[type](msg, { autoClose: 3000 });

  const scrollToResult = () => {
    setTimeout(() => resultRef.current?.scrollIntoView({ behavior: "smooth" }), 200);
  };

  const fetchMyActors = async () => {
    setLoading(true);
    try {
      const res = await axios.post(`${backend}/api/actors`, { apiKey });
      setActors(res.data.data?.items || []);
      notify('My Actors fetched', 'success');
    } catch {
      notify('Failed to fetch your actors', 'error');
      setActors([]);
    } finally { setLoading(false); }
  };

  const fetchPublicActors = async () => {
    setLoading(true);
    try {
      const res = await axios.get(`${backend}/api/actors/public`);
      setPublicActors(res.data.data?.items || []);
      notify('Public Actors fetched', 'success');
    } catch {
      notify('Failed to fetch public actors', 'error');
      setPublicActors([]);
    } finally { setLoading(false); }
  };

  const fetchSchema = async id => {
    setLoading(true);
    try {
      const res = await axios.post(`${backend}/api/schema`, { actorId: id, apiKey });
      setSchema(res.data);
      setActorId(id);
      notify('Schema loaded', 'success');
      const s = res.data.inputSchema || {};
      const sample = Object.fromEntries(Object.keys(s).map(k => {
        const t = s[k].type;
        return [k, t === 'string' ? "" : t === 'boolean' ? false : t === 'array' ? [] : t === 'object' ? {} : null];
      }));
      setInput(Object.keys(sample).length ? JSON.stringify(sample, null, 2) : '{}');
    } catch {
      notify('Failed to load schema', 'error');
      setSchema(null);
    } finally { setLoading(false); }
  };

  const runActor = async () => {
    setLoading(true);
    try {
      const parsed = JSON.parse(input);
      const res = await axios.post(`${backend}/api/run`, { actorId, apiKey, input: parsed });
      setRunResult(res.data);
      notify('Actor run successful', 'success');
      scrollToResult();
    } catch {
      notify('Failed to run actor', 'error');
      setRunResult(null);
    } finally { setLoading(false); }
  };

  return (
    <div className="min-h-screen px-4 py-8 bg-darkBg text-lightText">
      <ToastContainer />
      <div className="max-w-4xl mx-auto space-y-8">
        <div className="flex justify-between items-center">
          <h1 className="text-3xl md:text-4xl font-bold" style={{ color: '#58a6ff' }}>
            🔌 Apify Integration Dashboard
          </h1>
          <DocsModal />
        </div>

        <div className="flex flex-col sm:flex-row sm:items-center gap-4">
          <input
            type="text"
            placeholder="Apify API Key"
            value={apiKey}
            onChange={e => setApiKey(e.target.value)}
            className="flex-1 px-4 py-2 bg-darkSurface border border-gray-600 rounded text-lightText focus:ring-2 focus:ring-accentBlue placeholder-gray-400"
          />
          <div className="flex flex-col gap-2 sm:flex-row sm:gap-2 sm:mt-0 mt-2">
            <button
              onClick={fetchMyActors}
              disabled={loading}
              className="px-4 py-2 bg-accentGreen hover:bg-green-600 text-white rounded shadow transition hover:scale-105"
            >
              My Actors
            </button>
            <button
              onClick={fetchPublicActors}
              disabled={loading}
              className="px-4 py-2 bg-accentBlue hover:bg-blue-500 text-white rounded shadow transition hover:scale-105"
            >
              Public Actors
            </button>
          </div>
        </div>

        {loading && <div className="text-center"><ClipLoader color="#58a6ff" /></div>}

        <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
          <ActorList title="🎭 My Actors" actors={actors} fetchSchema={fetchSchema} />
          <ActorList title="🌐 Public Actors" actors={publicActors} fetchSchema={fetchSchema} />
        </div>

        {schema && (
          <SchemaEditor
            actorId={actorId}
            input={input}
            setInput={setInput}
            loading={loading}
            runActor={runActor}
          />
        )}

        {runResult && (
          <div ref={resultRef} className="bg-darkSurface border border-green-700 p-6 rounded space-y-4">
            <h2 className="text-lg font-semibold text-green-400">Run Result</h2>
            <div className="max-h-[400px] overflow-auto bg-darkBg p-3 rounded">
              <JSONViewer src={runResult} collapsed={1} theme="monokai" enableClipboard={false} />
            </div>
          </div>
        )}
      </div>
    </div>
  );
}